<template>
  <div id="change-password-component">
    <el-card
      class="card"
      :body-style="{
        padding: '20px 50px'
      }"
    >
      <div class="title-container">
        <p>Invitation</p>
      </div>
      <div class="main">
        <div class="item">
          <p>Email</p>
          <el-input type="email" v-model="form.email"></el-input>
        </div>
        <div class="item">
          <p>Brand</p>
          <el-select v-model="form.brand">
            <el-option value="Nike" placeholder="Select">Nike</el-option>
          </el-select>
        </div>
        <div class="item">
          <p>Business unit</p>
          <el-input v-model="form.businessUnit"></el-input>
        </div>
        <div class="item">
          <p>Text</p>
          <el-input type="textarea" rows="5" v-model="form.text"></el-input>
        </div>
      </div>
      <div class="footer">
        <el-button type="success" @click="onSendClick">Send</el-button>
      </div>
    </el-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { postCreateInvitation } from "@/apis/auth";
import { ElNotification } from "element-plus";

const Component = defineComponent({
  name: "ChangePassword",
  setup() {
    const form = reactive({
      email: "",
      brand: "",
      businessUnit: "",
      text: ""
    });

    const onSendClick = async () => {
      const params = {
        email: form.email,
        message: form.text,
        company: 1
      };
      try {
        const { data } = await postCreateInvitation(params);
        ElNotification({
          title: "Success",
          type: "success",
          message: `Send Invitation to ${data.email} successful.`
        });
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: `Can't send Invitation.`
        });
      }
    };

    return {
      form,

      onSendClick
    };
  }
});

export default Component;
</script>

